* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  color: #000000e6;
  font-optical-sizing: auto;
  background-color: #ddd;
  font-family: YS Text, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
}

body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.loader {
  color: #fff;
  z-index: 999;
  background-color: #209cee;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: none;
  position: absolute;
  inset: 0;
}

.loader.visible {
  display: flex;
}

.loader h1 {
  font-weight: 800;
}

.loader .logo {
  animation: 2s linear infinite zoom-animation;
}

.card {
  background-color: #fff;
  flex-direction: column;
  gap: 12px;
  width: 100vw;
  max-width: 384px;
  height: 100vh;
  max-height: 384px;
  padding: 12px 16px;
  display: flex;
  overflow: hidden;
}

.card h2 {
  text-transform: uppercase;
  text-align: center;
  font-size: 18px;
}

.card .description {
  overflow: hidden;
}

.card .description p {
  text-align: justify;
  font-size: 12px;
  line-height: 1.5em;
}

@keyframes zoom-animation {
  0% {
    transform: scale(.8);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(.8);
  }
}
